import Button from '../../../../Input-controls/Button/Button'
import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux';
import { withRouter } from 'react-router';

function MpinInfoModel(props) {

    const handleproceed=()=>{
        props.closeClicked()
    }

    return (
        <>
			<div className="pp_prnt_1"/>
            <div className="pp_cls-invite">
                
                <div className="mpinNote" style={{marginTop:'0px', fontSize:'15px'}}>
                    <div>
                        <div>Text here</div>
                    </div>
                </div>

                <div style={{marginTop: '30px', textAlign:'center'}}>
                    <Button value="I, Understand" onClick={()=>handleproceed()} />
                </div>
            </div> 
        </>
    )
}



const mapStateToProps = (state) => {
    return {
      mid:state.userstate.mid,
  }}
  

  
  
  export default withRouter(connect(mapStateToProps, null)(MpinInfoModel))