import Button from '../../../../Input-controls/Button/Button'
import React, { useEffect, useState } from 'react'
import InfoBox from '../../../../components/Common/InfoBox/InfoBox'
import Input from '../../../../Input-controls/Input/Input'
import { heightWidthCalc, outTxtByTime } from '../../../../utils/Api'
import WhyCompnay from '../../../Informations/whyCompnay'
import { pageURL, urlPrefixEmpSure } from '../../../../utils/MicroServiceURL'
import Axios from 'axios'
import MpinModel from './MpinModel'
import { notify } from '../../../../Input-controls/Toast/Toast'
import { actions as stateAction } from '../../../../redux/ducks/state_change'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { urls } from '../../../../routes/routes'
import { withRouter } from 'react-router'
import { actions as gstActions } from '../../../../redux/ducks/gst'

function MagicPin(props) {

    const [state, setstate] = useState({
        timeBasedText:'',
        gstin:''
    })
    const[showInfo, setShowInfo]=useState(false)
    const [showMpinModel, setshowMpinModel] = useState(true)

    useEffect(() => {
        setstate({...state, timeBasedText: outTxtByTime()});
    }, [])

    const HandleProceed=()=>{
        Axios.get(urlPrefixEmpSure(`/employer/search-employer?channel=magicpin&mid=${props.mid}&gstin=${props.state.GstNumber}`), 
            { 
            headers:{
                access_key:'token dpHau891H5uKt5ACrGKHENIuQLGL2JkD',
                Authorization:`bearer ${localStorage.getItem('token')}`
            },
        })
        .then(res=>{
            if(res.data.success==1){
                props.updateIdentifier(res.data.data.identifier)
                props.updateGstin(res.data)
                props.history.push(urls.COMPANY_DETAILS);
            }
        })
        .catch(err=>{
            if(err.response && err.response.data){
                notify.danger(err.response.data.msg)
            }else{
                notify.danger("Something went wrong. Please contact onsurity")
            }
        })
    }

    const handleChange=(e)=>{
        const{name, value}=e.target;
        props.setState('gstDetails',name, value);
        props.setState('gstDetails','CompanyName', "");
    }

    const HandleMpinProceed=(mpin)=>{
        Axios.get(urlPrefixEmpSure(`/employer/acq-channel/magicpin/${mpin}`),
            { 
            headers:{
                access_key:'token dpHau891H5uKt5ACrGKHENIuQLGL2JkD',
                Authorization:`bearer ${localStorage.getItem('token')}`
            },
        })
        .then(res=>{
            if(res.data.success==1){
                if(res.data.data.gstin){
                    props.setState('gstDetails',"GstNumber", res.data.data.gstin);
                    props.setState('gstDetails','CompanyName', "");
                }else{
                    props.setState('gstDetails',"GstNumber", "");
                }
                props.updateMid(mpin)
                setshowMpinModel(false)
            }
        })
        .catch(err=>{
            if(err.response.data){
                notify.danger(err.response.data.msg)
            }else{
                notify.danger("Id entered is Invalid. Please contact onsurity")
            }
        })
    }


    return (
        <>

        {showMpinModel &&
            <MpinModel
                closeModel={()=>setshowMpinModel(false)}
                proceedClicked={(mpin)=>HandleMpinProceed(mpin)}/>}

         <InfoBox showInfo={showInfo} setShowInfo={(bool)=>setShowInfo(bool)}>
            <WhyCompnay/>
         </InfoBox>
        <div className="PurchaseMainSection">
               <div className="GSTINContent">
                   <div className="mt-3 mb-4">
                       <span className="HeadingPurchase">{"Hi, "+state.timeBasedText+" :)"}</span>
                   </div>

                   <div style={{marginTop:'50px'}}>
                        <Input
                            type="text"
                            name="GstNumber"
                            value={props.state.GstNumber}
                            onChange={handleChange}
                            label="GSTIN number"
                            //    help="For Others"
                            //    err={apiError.gstError}
                            width={heightWidthCalc().width<360?"240px":"280px"}
                            placeholder="Enter your GSTIN number"
                            HelpClicked={()=>{
                                setShowInfo(true)
                                }}
                                />
                        {!props.state.GstNumber &&
                        <div className="noteGstin">(Enter your GSTIN to get GST Invoice)</div>}
                   </div>
               </div>
           </div>
           <div className="PurchaseButtonSection">
               <Button
                   onClick={HandleProceed}
                //    disabled={(props.state.validGstNumber==false && props.companyData.selectedCompany == null) && 'disabled'}
                   className="mt-1"
                   value="Proceed"/>
           </div>
           </>
    )
}

const mapStateToProps = (state) => {
    return {
      gstData:state.gst,
      state:state.userstate.gstDetails,
      companyData: state.search_company,
      mid:state.userstate.mid,
  }}
  
  const mapDispatchToProps = (dispatch) => ({
      ...bindActionCreators({
          ...stateAction,
          ...gstActions
          //ADD IF ITS FROM DIFFRENT ACTION FILE
      }, dispatch)
    });
  
  
  export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MagicPin))